	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/3/don-norman-design-thinking/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>In June, 2010, I posted an essay on Core77 entitled ”<a href=\"http://www.core77.com/blog/columns/design_thinking_a_useful_myth_16790.asp\">Design Thinking: A Useful Myth</a>.” Since my essay was posted, I keep encountering people who jump to solutions and who fail to question assumptions—engineers, business people, and yes, designers (and design students). These encounters made me reconsider. I observed design students who were acting mindlessly, simply doing their assignments as presented. No creativity, no imagination, no questioning. That’s not what design thinking is about. As a result, I have changed my mind: Design Thinking really is special.</p>\n</blockquote>\n<p>When Don Norman talks about design, you listen.</p>","frontmatter":{"title":"Don Norman: Design thinking","date":"March 25, 2013","passthroughUrl":"http://www.core77.com/blog/columns/rethinking_design_thinking_24579.asp?utm_source=loopinsight.com&utm_medium=referral&utm_campaign=Feed"}}},"pathContext":{"slug":"/blog/2013/3/don-norman-design-thinking/","previous":{"fields":{"slug":"/blog/2013/3/google-chairman-eric-schmidt-admits-using-a-blackberry/"},"frontmatter":{"title":"Google chairman Eric Schmidt admits using a BlackBerry","passthroughUrl":"http://www.guardian.co.uk/technology/2013/mar/21/eric-schmidt-blackberry-user","type":null}},"next":{"fields":{"slug":"/blog/2013/3/yahoo-buys-summly-then-shuts-it-down/"},"frontmatter":{"title":"Yahoo buys Summly, then shuts it down","passthroughUrl":"http://ycorpblog.com/2013/03/25/yahoo-to-acquire-summly/","type":null}}}}

